import React from 'react'
import { Player, Controls } from '@lottiefiles/react-lottie-player';
import { Link } from 'react-router-dom'

export const ScreenZero = () => {
  const data = localStorage.getItem("app") ?? null

  return (
    <div>
      <h1 className="title">Appka for Kupcha</h1>
      <p>цэ такэ шуточнэ, и нэ дуже, приложение, так сказать...</p>
      <Player
        autoplay
        loop
        src="https://assets6.lottiefiles.com/packages/lf20_elqubb3f.json"
        style={{ height: '300px', width: '300px' }}
      >
        <Controls visible={false} buttons={['play', 'repeat', 'frame', 'debug']} />
      </Player>
      <Link className="button__link margin1" to="/step-one">Go!</Link>

      {data !== null && <h1>Results:</h1>}
      {data !== null && JSON.parse(data).map((el) => {
        return <div>
          <p>{el.question} -  <span>{el.answer}</span></p>
        </div>
      })}
    </div>
  )
}
