import './App.css';
import { ScreenOne } from './pages/ScreenOne';
import { Routes, Route, BrowserRouter } from 'react-router-dom';
import { ScreenTwo } from './pages/ScreenTwo'
import { ScreenThree } from './pages/ScreenThree';
import { useLocation } from "react-router-dom";
import { useEffect } from 'react';
import { ScreenFour } from './pages/ScreenFour';
import { ScreenFive } from './pages/ScreenFive';
import { ScreenZero } from './pages/ScreenZero';
import { ScreenSix } from './pages/ScreenSix';
import { ScreenSeven } from './pages/ScreenSeven';
import track from './music/EveryTimeYouGoAway.mp3'
import trackCongrats from './music/track.mp3'
import trackFail from './music/49dfdef97d65e09.mp3'
import { Congrats } from './pages/Congrats';
import { Fail } from './pages/Fail';

function App() {
  function ScrollToTop() {
    const { pathname } = useLocation();
    useEffect(() => {
      window.scrollTo(0, 0);
    }, [pathname]);
    return null;
  }

  const palayAudio = () => {
    const player = document.getElementById("audio")
    if (player.paused) {
      player.play()
    } else {
      player.pause()
    }
  }

  return (
    <div className="App">
      <audio  id="audio"><source src={track} type="audio/mpeg"></source></audio>
      <button className="audio" onClick={() => palayAudio()}> ▶ </button>
      
      <audio  id="audio2"><source src={trackCongrats} type="audio/mpeg"></source></audio>
      <audio  id="audio3"><source src={trackFail} type="audio/mpeg"></source></audio>

      <BrowserRouter>
        <ScrollToTop />
        <Routes>
          <Route path={'/'} element={<ScreenZero />} />
          <Route path={'/step-one'} element={<ScreenOne />} />
          <Route path={'/step-two'} element={<ScreenTwo />} />
          <Route path={'/step-three'} element={<ScreenThree />} />
          <Route path={'/step-four'} element={<ScreenFour />} />
          <Route path={'/step-five'} element={<ScreenFive />} />
          <Route path={'/step-six'} element={<ScreenSix />} />
          <Route path={'/step-seven'} element={<ScreenSeven />} />
          <Route path={'/congrats'} element={<Congrats />} />
          <Route path={'/fail'} element={<Fail />} />
        </Routes>
      </BrowserRouter>
    </div>
  );
}

export default App;
