import React, { useEffect, useState } from 'react'
import { useNavigate } from 'react-router-dom'
import { Player, Controls } from '@lottiefiles/react-lottie-player';

export const ScreenTwo = () => {
  const [counter, setCounter] = useState(0)
  const navigate = useNavigate()

  const handleStepTwo = () => {
    setCounter(prev => (prev + 1))
  }

  useEffect(() => {
    if (counter >= 3) {
      setTimeout(() => {
        navigate('/step-three')
      }, 1400)
    }
  }, [counter, navigate])

  return (
    <div className="second__screen">
      <h1>
        {counter === 0 && "Lass uns spielen"}
        {counter === 1 && "Ha-Ha-Ha"}
        {counter >= 2 && "versuchen Sie es nochmal"}
      </h1>
      <Player
        autoplay
        loop
        src="https://assets8.lottiefiles.com/private_files/lf30_aYiP6b.json"
        style={{ height: '300px', width: '300px' }}
      >
        <Controls visible={false} buttons={['play', 'repeat', 'frame', 'debug']} />
      </Player>
      <div onClick={handleStepTwo} className={`button__link ${counter >= 1 && 'trick1'} ${counter >= 2 && 'trick2'} ${counter >= 3 && 'trick3'}`}>
        {counter === 0 && "Lasst uns"}
        {counter === 1 && "Upsi-dupsi"}
        {counter === 2 && "Weiter"}
      </div>
    </div>
  )
}
