import React, { useState } from 'react'
import { Player, Controls } from '@lottiefiles/react-lottie-player';
import { useNavigate } from 'react-router';

export const ScreenFive = () => {
  const [select1, setSelect1] = useState()
  const [select2, setSelect2] = useState()
  const [select3, setSelect3] = useState()
  const [inp1, setInp1] = useState("")
  const [inp2, setInp2] = useState("")
  const navigator = useNavigate()
  const handleButtonDone = (e) => {
    e.preventDefault()
    const data = [
      {question: "Расскажи чего тебе хочется", answer: inp1},
      {question: "Что делает нас сильнее?", answer: inp2},
      {question: "3",answer: select1},
      {question: "4",answer: select2},
      {question: "5", answer: select3}
    ]
    localStorage.setItem("app", JSON.stringify(data))
    navigator("/step-six")
  }
  return (
    <div>
      <Player
        autoplay
        loop
        src="https://assets7.lottiefiles.com/packages/lf20_qsrtwdyv.json"
        style={{ height: '300px', width: '300px' }}
      >
        <Controls visible={false} buttons={['play', 'repeat', 'frame', 'debug']} />
      </Player>
      <form onSubmit={handleButtonDone}>
        <div class="field">
          <input value={inp1} onChange={(e) => setInp1(e.target.value)} required={true} type="text" name="text" id="text" placeholder="jane.appleseed@example.com" />
          <label for="text">Расскажи чего тебе хочется</label>
        </div>

        <div className="field">
          <input value={inp2} onChange={(e) => setInp2(e.target.value)} required={true} type="text2" name="text2" id="text2" placeholder="jane.appleseed@example.com" />
          <label for="text2">Что делает нас сильнее?</label>
        </div>

        <label className="select__label" for="sel1">Давай жить в здравии?</label>
        <select name="sel1" onChange={(e) => setSelect1(e.currentTarget.value)} required={true} value={select1} className="select">
          <option value="">Please select</option>
          <option>Давай</option>
          <option>Мы и так в здравии</option>
          <option>Нет</option>
          <option>Омг</option>

        </select>

        <label className="select__label" for="sel2">Да?</label>
        <select name="sel2" onChange={(e) => setSelect2(e.currentTarget.value)} required={true} value={select2} className="select">
          <option value="">Please select</option>
          <option>Да-да-да </option>
          <option>нет-нет-нет</option>
          <option>ДАааааааа</option>
        </select>

        <label className="select__label" for="sel3">Просто выбери</label>
        <select name="sel3" onChange={(e) => setSelect3(e.currentTarget.value)} required={true} value={select3} className="select">
          <option value="">Please select</option>
          <option>круглое </option>
          <option>черное</option>
          <option>квадратное</option>
        </select>
        <button className="button__link3">Los!</button>
      </form>

    </div>
  )
}
