import React, { useEffect } from 'react'
import { Link } from 'react-router-dom'
import { useState } from 'react/cjs/react.development'

export const ScreenSeven = () => {
  const [state, setState] = useState(true)
  const [show, setShow] = useState(false)

  useEffect(() => {
    setTimeout(() => {
      setState(false)
      document.body.style.background = "#000"
    }, 10000)

    setTimeout(() => {
      setShow(true)
    }, 17000)

  }, [state])

  const stop = () => {
    const player = document.getElementById("audio")
    player.pause()
  }


  const playCongrats = () => {
    stop()
    const player = document.getElementById("audio2")
    player.play()
  }

  const playFail = () => {
    stop()
    const player = document.getElementById("audio3")
    player.play()
  }
  return (
    <>
      {state && <> <div class="ticker-wrap">
        <div class="ticker">
          <div class="ticker__item">Алена Купченко</div>
        </div>
      </div>

        <div class="ticker-wrap2">
          <div class="ticker">
            <div class="ticker__item2">Она же Алеша Пупченко</div>
          </div>
        </div>

        <div class="ticker-wrap3">
          <div class="ticker">
            <div class="ticker__item3">Achtung !%#%^ Achtung #%*@% Achtung @#$%#@!</div>
          </div>
        </div> </>}

      {!state &&
        <>
          <div className="ticker-wrap4">
            <div className="ticker2">
              <div className="ticker__item4">ns dsqltim pf tvyz Pfvt; </div>
            </div>
          </div>

          {show &&
            <>
              <Link onClick={playCongrats} className="button__link margin2" to='/congrats'>Yes!!!</Link>
              <Link onClick={playFail} className="button__link " to='/fail'>No...</Link>
            </>}
        </>}
    </>
  )
}
